import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/record',
      name: 'taskList',
      component: (resolve) => {
        require(['@/view/taskList'], resolve)
      },
      meta: {
          title: '操作记录'
      }
    },
    {
      path: '/',
      redirect:{ name: 'taskList' }
    },
    {
      path: '/member',
      name: 'memberList',
      component: (resolve) => {
        require(['@/view/memberList'], resolve)
      },
      meta: {
          title: '成员管理'
      }
    },
    {
      path: '/workshop',
      name: 'workshopState',
      component: (resolve) => {
        require(['@/view/workshopState'], resolve)
      },
      meta: {
          title: '选择当前车间状态'
      }
    },
    {
      path: '/carspace',
      name: 'carSpace',
      component: (resolve) => {
        require(['@/view/carSpace'], resolve)
      },
      meta: {
          title: '管理车间'
      }
    }
  ]
})



